import React, { useState, useEffect } from 'react';
import EmptyPoster from '../../img/empty_poster.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import { Center, SearchBar, Title, ItemWrapper, Item, SubTitle, GoBack, Box } from '../styles/searchstyles';
import Grid from '@material-ui/core/Grid';
import Logo from '../../img/iko2.png';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import CollectionsBookmarkIcon from '@material-ui/icons/CollectionsBookmark';
import { Link } from 'react-router-dom';

function Search(props) {
  const [error, setError] = useState(null);
  const [items, setItems] = useState([]);
  const [query, setQuery] = useState('');
  const [imageOp, setImageOp] = useState(true);
  const api_key = '58666352553834b5820ef436680fb2e5';

  const handleChange = (e) => {
    setQuery(e.target.value);
    setImageOp(false);
  };
  const havePhoto = ({ media_type, poster_path, profile_path, name, title }) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        return <img alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
      } else {
        return <img alt={title} src={EmptyPoster} />;
      }
    } else if (media_type === 'person') {
      if (profile_path) {
        return <img alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };

  useEffect(() => {
    if (query) {
      fetch(`https://api.themoviedb.org/3/search/multi?api_key=${api_key}&language=pl-PL&query=${query}&page=1&include_adult=false`)
        .then((res) => res.json())
        .then(
          (result) => {
            setItems(result.results);
          },
          (error) => {
            setError(error);
          }
        );
    }
    document.title = 'InfCinema';
  }, [query]);
  if (error) console.log(error);
  return (
    <>
      <GoBack>
        <ArrowBackIcon
          fontSize='large'
          onClick={() => {
            if (props.history.length > 2) {
              props.history.goBack();
              // setBug(Math.random());
            } else props.history.replace('');
          }}
        />
      </GoBack>
      <Center>
        {imageOp && <img src={Logo} alt='Logo' width='200px' style={{ marginTop: '-20px', marginBottom: '-50px' }} />}
        <Title>InfCinema {!imageOp && <img src={Logo} alt='Logo' width='100px' />}</Title>
        <SubTitle>Wyszukaj Film, Serial lub Aktora</SubTitle>
        {/* <form autoComplete='off' onSubmit={(e) => e.preventDefault()}> */}
        <SearchBar autoComplete='off' id='standard-basic' label='Wyszukaj' onChange={handleChange} />
        {/* </form> */}
        <ItemWrapper>
          {items && query ? (
            items.slice(0, 24).map((item, i) => {
              if (item.media_type === 'movie') {
                return (
                  <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.title.toLowerCase().split(' ').join('-')}`)} key={i}>
                    {havePhoto(item)}
                    {item.title && <div className='tit'>{item.title}</div>}
                  </Item>
                );
              } else if (item.media_type === 'tv') {
                return (
                  <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.name.toLowerCase().split(' ').join('-')}`)} key={i}>
                    {havePhoto(item)}
                    {item.name && <div className='tit'>{item.name}</div>}
                  </Item>
                );
              } else if (item.media_type === 'person') {
                return (
                  <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.name.toLowerCase().split(' ').join('-')}`)} key={i}>
                    {havePhoto(item)}
                    {item.name && <div className='tit'>{item.name}</div>}
                  </Item>
                );
              } else {
                return 0;
              }
            })
          ) : (
            <>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  margin: '20px',
                  width: '100%',
                  color: '#042745',
                  fontWeight: 'bold',
                  fontSize: '20px',
                }}>
                lub
              </div>
              <Grid container spacing={0} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <Grid item xs={12} sm={6} md={4}>
                  <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', margin: '20px' }}>
                    <Box to='/browse'>
                      <CollectionsBookmarkIcon />
                      Przeglądaj
                    </Box>
                  </div>
                </Grid>
              </Grid>
            </>
          )}
        </ItemWrapper>
      </Center>
    </>
  );
}

export default Search;
